import React from "react";
import AppBar from '../../components/Appbar';
import Slideshow from '../../components/Slideshow';
import { Container, ContainerSlideshow, DivContainer, Button, LinkButton } from "./style";

const properties = {
duration: 5000,
transitionDuration: 500,
infinite: true,
indicators: true,
arrows: true
}

const Page = () => {
  return (
    <div>
      <AppBar />
      <Container>
        <DivContainer>
          <LinkButton to={`/produto/create`}>
            <Button>Novo produto</Button>
          </LinkButton>
        </DivContainer>
        <DivContainer>
          <LinkButton to={`/produto`}>
            <Button>Verificar estoque</Button>
          </LinkButton>
        </DivContainer>
      </Container>

      <ContainerSlideshow>
        <Slideshow></Slideshow>
      </ContainerSlideshow>
    </div>
  );
};
export default Page;
