import React from 'react';
import { Slide } from 'react-slideshow-image';
import 'react-slideshow-image/dist/styles.css'

import slide_1 from '../../assets/img/slide_1.png';
import slide_2 from '../../assets/img/slide_2.png';
import slide_3 from '../../assets/img/slide_3.png';

import {Image} from './style'

const Slideshow = () => {
    return (
      <div className="slide-container">
        <Slide>
          <Image className="each-slide">
            <Image style={{'backgroundImage': `url(${slide_1})`}}>
            </Image>
          </Image>
          <div className="each-slide">
            <Image style={{'backgroundImage': `url(${slide_2})`}}>
            </Image>
          </div>
          <div className="each-slide">
            <Image style={{'backgroundImage': `url(${slide_3})`}}>
            </Image>
          </div>
        </Slide>
      </div>
    )
}

export default Slideshow;